<template>
  <div class="hello">
    <h1>{{ msg }}</h1>

    <button @click="getList">getList</button>

  </div>
</template>

<script>
  import util from '../common'
export default {
  name: 'home',
  data () {
    return {
      msg: 'home'
    }
  },
  methods:{
    getList(){
      util.getBlogList()
        .then(data =>{
          debugger
        console.log(data);
      })
        .catch(err =>{
          console.log(err)
        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



